.classes {
    position: relative;
    .article--hero_left {
        @include article-hero-left(url('/img/article/classes-hero.jpg'));
    }

    nav {
        display: flex;
        justify-content: center;

        a {
            margin: 0 0.25em;
            color: #ff4c4d;
            font-size: 16px;
            font-weight: 500;

            @include mq(small) {
                margin: 0 1em;
                font-size: 18px;
            }
        }
    }
}

.our-classes {
    margin-top: 3.75em;
    max-width: 700px;

    .class-item {
        max-width: 675px;
        margin-bottom: 50px;
    }

    h4 {
        color: $primaryPink;
        font-size: 18px;
        font-weight: 500;
        opacity: 0.5;
    }

    h2 {
        margin-top: 7.5px;
        margin-bottom: 20px;
        font-size: 28px;
        font-weight: 600;
        color: $primaryPink;
    }

    &_sweetener-1 {
        display: none;

        @include mq(small) {
            position: absolute;
            right: 0;
            top: 25%;
            display: block;
            width: 250px;
        }

        @include mq(medium) {
            width: 375px;
        }
    }
    &_sweetener-2 {
        display: none;

        @include mq(small) {
            position: absolute;
            left: 0;
            top: 55%;
            display: block;
            width: 175px;
        }

        @include mq(medium) {
            width: 325px;
        }
    }
}

.is-checked {
    color: #cc1414 !important;
    text-decoration: underline;
}
